.App {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  

}

header {
  background-color: #333;
  color: white;
  padding: 20px;
  text-align: center;
  border-radius: 5px;
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}

nav a {
  color: white;
  text-decoration: none;
  padding: 10px 20px;
  display: block;
}

nav a:hover,
nav a.active {
  background-color: green;
}

main {
  padding: 20px;
}

footer {
  text-align: center;
  color: white;
  padding: 20px;
}



.terminal {
  background-color: #333;
  color: #fff;
  padding: 20px;
  border-radius: 10px;
  
}

.terminal-input {
  display: flex;
  align-items: center;
  margin-top: 20px;
}

.terminal-input input {
  background-color: transparent;
  color: #fff;
  border: none;
  padding: 10px;
  font-size: 16px;
  outline: none;
  
}


.terminal-output{
  font-family: "Courier New", Courier, monospace;
  background-color: black;
  color: green;
  padding: 20px;
  margin: 0;
  border-radius: 5px;
}



.cursor {
  width: 2px;
  background-color: #000;
  animation: blink 1s cubic-bezier(0, 0, 0, 1) infinite;
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}



.colorhelp{
  text-decoration: none;
  color: #008CBA;
}

body {
  background-color: rgb(0, 0, 0);
