pre {
  margin: 10px;
}



.close{
  background-color: #100f0f;
  color: white;
  padding: 0px;
  text-align: -webkit-auto;
  border-radius: 8px;
}

.close-button{
  background-color: #f12121;
  border-radius: 51%;
  border: none;
  content: "×";
  color: #070707;
  margin-right: 9px;
 
}

.close-button2{
