}











/* Mobile responsiveness */
@media only screen and (max-width: 600px) {
  .terminal {
    font-size: 14px;
  }

  .terminal p {
    font-size: 12px;
  }
  .terminal-output {
    font-size: 15px;
  }
  .footer-text {
    font-size: 12px;
  }

  .cursor {
    display: none;
  }

  .terminal-input {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .terminal-input input {
    width: 100%;
  }

  .close {
